import React,{Component,Fragment} from "react";
import axios from 'axios';
import $ from "jquery";
import {Common} from "../../Cesium/method";

class ResourceMap extends Component{
    constructor(props) {
        super(props);
        this.state={
            treeList:{},
            TopList: {},
            titlename: '',
            deleteId:'',//删除ID
        };
        ResourceMap.this = this;
    }
    componentDidMount() {
        this.onTopList();
    }

    //获取网格列表信息
    onLoad() {
        var list = [];
        axios.get(global.Url + 'grid/region/list').then((res) => {
            const result = res.data.data;
            if (result) {
                list = result;
                axios.post(global.Url + 'grid/info/list').then((res) => {
                    const results = res.data.data;
                    if (results) {
                        //console.log(results);
                        results.forEach(element => {
                            var arr = {
                                id: element.id
                                , pid: element.region_id
                                , region_name: element.grid_name
                                , node_type: "details"
                                , position: element.position
                                , indoor: element.indoor
                                , build_id: element.build_id
                                , floor_id: element.floor_id
                                , children: [],
                                grid_style: element.grid_style,
                                geom: element.geom
                            }
                            list.push(arr);
                        });
                        axios.post(global.Url + 'device/info/deviceByGrid', { category_id: global.titid }).then((res) => {
                            const resultss = res.data.data;
                            if (resultss) {
                                //console.log(resultss);
                                resultss.forEach(element => {
                                    var arr = {
                                        id: element.device_code
                                        , pid: element.grid_id
                                        , region_name: element.device_name
                                        , node_type: "last"
                                        , position: null
                                        , indoor: element.indoor
                                        , build_id: element.build_id
                                        , floor_id: element.floor_id
                                        , children: [],
                                        grid_style: null,
                                        geom: element.geom,
                                        lastData:{element}
                                    }
                                   // console.info(arr);
                                    list.push(arr);
                                });
                               // console.info(list);
                                this.setState({
                                    treeList: list
                                })

                            } else {
                                alert(res.data.msg)
                            }

                        })

                    } else {
                        alert(res.data.msg)
                    }
                })


            } else {
                alert(res.data.msg)
            }
        })
    }

    //生成资源图谱列表信息
    onTopList(){
        axios.get(global.Url+'device/info/countForCategory').then((res) => {
            const results = res.data.data;
            //console.log(results);
            if(results && res.data.msg === 'success') {
                this.setState({
                    TopList: results
                })
            }else{
                alert(res.msg)
            }
        })
    }

    //树结构生成
    AnalyticFormat(vdom) {
        var menuObj = vdom;
        //转成树
        function getTree(data, Pid) {
            let result = [];
            let temp;
            for (let i = 0; i < data.length; i++) {
                if (data[i].pid === Pid) {

                    temp = getTree(data, data[i].id);

                    data[i].children = temp;
                    result.push(data[i])
                }
            }
            return result

        }

        return getTree(menuObj, "0");

    }
    generateMenu(menuObj) {
        let vdom = [];
        var DataNum=0;
        if (menuObj instanceof Array) {
            let list = [];
            for (var item of menuObj) {
                list.push(this.generateMenu(item));
            }
            vdom.push(
                <ul key="single">
                    {list}
                </ul>
            );
        } else {
            if(menuObj == null){
                return;
            }
            console.log(menuObj)
            if(menuObj.children.length>0){
                for(var i=0; i<menuObj.children.length;i++){
                    DataNum += menuObj.children[i].children.length;
                }
            }
            vdom.push(
                <li key={menuObj.id} className='newMap' id={'newMap'+menuObj.id} >
                    <span onClick={(e)=>this.onMenuClicked(e,menuObj)}>
                        {menuObj.node_type==='last'?'':<i></i>}
                        {menuObj.region_name}
                        {menuObj.node_type==='last'?'':<em>
                            {menuObj.pid==='0'?DataNum:menuObj.children.length}

                        </em>}
                    </span>

                    {this.generateMenu(menuObj.children)}
                </li>
            );
        }
        return vdom;
    }
    //点击子类
    onMenuClicked(ev,name) {
        ev.stopPropagation();
        // 被点击的<h1>
        let node = $(ev.target);

        // 属于<h1>的相邻子菜单列表
        let subMenu = node.siblings('ul');

        // 显示/隐藏这个列表
        subMenu.css("display", subMenu.css('display') === "none" ? "block" : "none");
        $("#newMap"+ name.id).parents().find('li').find('span').css({'color':'#fff','fontWeight':'100'});
        $("#newMap"+ name.id).parents().find('span').find('i').removeClass('only');
        $("#newMap"+ name.id).find('span').css({'color':'#54b2df','fontWeight':'bold'});
        $("#newMap"+ name.id).find('span').find('i').addClass('only');
        $("#newMap"+ name.id).siblings().find('span').css({'color':'#fff','fontWeight':'100'});
        $("#newMap"+ name.id).siblings().find('span').find('i').removeClass('only');
        $("#newMap"+ name.id).children().find('span').css({'color':'#fff','fontWeight':'100'});
        $("#newMap"+ name.id).children().find('span').find('i').removeClass('only');
/*        {id:'',x:,y:,z:,h:,p:,r:,}*/
        if(name.node_type === 'last'){
            let data={
                id:name.id,
            }
            Common.CoordinateModelID(data);
        }

    }
    //图谱按钮事件
    AssignedLabelID(item) {
        //console.info(item);
        global.titid = item.id;
        let data1={
            id:ResourceMap.this.state.deleteId,
        }
        Common.removeBubbleLabel(data1);
        this.onLoad();
        this.setState({
            titlename:item.category_name + "列表",
            deleteId:item.id
        })
        $(".onlyNav"+ item.id).parents().css({'color':'#fff'});
        $(".onlyNav"+ item.id).css({'color':'#01F8FF'});
        $(".onlyNav"+ item.id).siblings().css({'color':'#fff'});
        $(".onlyNav"+ item.id).children().css({'color':'#fff'});
        let data={
            id:item.id,
            image:item.category_icon
        }
        Common.AddBubbleLabel(data);

    }

    render(){
        const { TopList, treeList, titlename} = this.state;
        return(
            <Fragment>
                <div className="AlarmShow resourceMap" style={{ 'height': 'calc(100vh - 125px)'}}>
                    <h1><span>资源图谱</span></h1>
                    <div className="ResourceMapList">
                        <button><i></i><span>资源图谱</span></button>
                        <ul>
                            {
                                TopList.length > 0 && TopList.map(
                                    (item,index) => {
                                        return  <li key={index} className={'onlyNav'+item.id} onClick={()=>this.AssignedLabelID(item)}>{item.category_name}({item.count})</li>
                                    }
                                )
                            }
                        </ul>
                    </div>
                    <div className="ResourceMapTree">
                        <h2><span>{titlename}</span></h2>
                        <div className='ResourceMapTreeUl'>
                            {this.generateMenu(this.AnalyticFormat(treeList))}
                        </div>
                    </div>
                </div>
            </Fragment>
        )
    }
}

export default ResourceMap;



