import React,{PureComponent,Fragment} from 'react';
import '../../../style/common.css';
import {actionCreators} from "../store";
import {connect} from "react-redux";
import {Common} from "../../Cesium/method";


let RelationalLock = {
    RainwaterSystem : true, //控制雨
    snowSystem : true, //控制雪
    Fogging : true, //控制雾
    enableLighting : true, //控制光照
    MeasurementHeight: true,//测高
    measureLineSpace:true,//测直线
    measureAreaSpace:true,//测面积
    onSurveillance:true,//虚拟化

};

class More extends PureComponent{
    constructor(props){
        super(props);
        this.state={
            activeBtn:false
        }
        More.this = this;
        this.changeStatus = this.changeStatus.bind(this)
    }
    changeStatus(){
        this.setState({
            activeBtn:!this.state.activeBtn
        })
    }
    //测高的双击事件
    MeasurementHeight(){
        if(RelationalLock.MeasurementHeight){
            RelationalLock.MeasurementHeight = false;
            Common.MeasurementHeight();
        }else{
            RelationalLock.MeasurementHeight = true;
            Common.RemoveHeight();
        }
    }
    //测直线的双击事件
    measureLineSpace(){
        if(RelationalLock.measureLineSpace){
            RelationalLock.measureLineSpace = false;
            Common.measureLineSpace();
        }else{
            RelationalLock.measureLineSpace = true;
            Common.RemoveLine();
        }
    }
    //测面积的双击事件
    measureAreaSpace(){
        if(RelationalLock.measureAreaSpace){
            RelationalLock.measureAreaSpace = false;
            Common.measureAreaSpace();
        }else{
            RelationalLock.measureAreaSpace = true;
            Common.RemoveArea();
        }
    }
    //雪的双击事件
    snowSystem(){
        if(RelationalLock.snowSystem){
            RelationalLock.snowSystem = false;
            Common.snowSystemV2();
        }else{
            RelationalLock.snowSystem = true;
            Common.snowDelete();
        }
    }
    //雨的双击事件
    RainwaterSystem(){
        if(RelationalLock.RainwaterSystem){
            RelationalLock.RainwaterSystem = false;
            Common.RainwaterSystemV2();
        }else{
            RelationalLock.RainwaterSystem = true;
            Common.RainDelete();
        }
        //alert('a');
    }
    //雾的双击事件
    Fogging(){
        if(RelationalLock.Fogging){
            RelationalLock.Fogging = false;
            Common.Fogging();
        }else{
            RelationalLock.Fogging = true;
            Common.fogDelete();
        }
    }
    //光照的双击事件
    enableLighting(){
        if(RelationalLock.enableLighting){
            RelationalLock.enableLighting = false;
            Common.enableLightingV2();
        }else{
            RelationalLock.enableLighting = true;
            Common.lightDelete();
        }
    }
    //虚拟化事件
   Surveillance(){
        if(RelationalLock.onSurveillance){
            RelationalLock.onSurveillance = false;
            Common.onSurveillance();
        }else{
            RelationalLock.onSurveillance = true;
            Common.onMap();
        }
    }
    render(){
        const {index,clickIcon,moreIndex,clickIconItem,clickIconItemNav} = this.props;
        const {activeBtn} = this.state;
        return(
            <Fragment>
                <li onClick={() => {clickIcon(3);this.changeStatus()}} className={activeBtn && index === 3  ? 'active' :''}>
                    <i className="icon3"></i>更多
                </li>
                {index === 3 && activeBtn ?
                    <div className="MoreShow">
                        <div className="MoreItem" onClick ={(even) => clickIconItem(even,1)} ><i className="more1"></i>图层</div>
                        <div className="MoreItem" onClick ={(even) => clickIconItem(even,2)} ><i className="more2"></i>漫游</div>
                        <div className="MoreItem" onClick ={(even) => clickIconItem(even,3)}  >
                            <i className="more3"></i>量测
                            {
                                moreIndex === 3 ?
                                    <div className="LcShow">
                                        <div className="LcItem" onClick ={(even) => clickIconItemNav(even,1)}>测高</div>
                                        <div className="LcItem" onClick ={(even) => clickIconItemNav(even,2)}>测距</div>
                                        <div className="LcItem" onClick ={(even) => clickIconItemNav(even,3)}>测面积</div>
                                    </div>
                                    : " "
                            }
                        </div>
                        <div className="MoreItem" onClick ={(even) => clickIconItem(even,4)}>
                            <i className="more4"></i>特效
                            {
                                moreIndex === 4 ?
                                    <div className="LcShow">
                                        <div className="LcItem" onClick ={(even) => clickIconItemNav(even,4)}><span>雨</span> <i className="LcIcon1"></i></div>
                                        <div className="LcItem" onClick ={(even) => clickIconItemNav(even,5)}><span>雪</span> <i className="LcIcon2"></i></div>
                                        <div className="LcItem" onClick ={(even) => clickIconItemNav(even,6)}><span>雾</span> <i className="LcIcon3"></i></div>
                                        <div className="LcItem" onClick ={(even) => clickIconItemNav(even,7)}><span>光照</span> <i className="LcIcon4"></i></div>
                                    </div>
                                    : " "
                            }
                        </div>
                        <div className="MoreItem" onClick ={(even) => clickIconItem(even,5)}><i className="more5"></i>飞行</div>
                        <div className="MoreItem" onClick ={(even) => clickIconItem(even,6)}><i className="more6"></i>虚拟化</div>
                        <div className="MoreItem" onClick ={(even) => clickIconItem(even,7)}><i className="more7"></i>地图信息</div>
                    </div>
                    : ''}
            </Fragment>
        )
    }
}

const mapState = (state) => {
    return{
        index:state.footer.get('index'),
        moreIndex:state.footer.get('moreIndex'),
        moreIndexItem:state.footer.get('moreIndexItem'),
    }
};

const mapDispatch = (dispatch) => ({
    clickIcon(item) {
        dispatch(actionCreators.click_Icon(item));
    },
    clickIconItem(e,item) {
        e.stopPropagation(); //阻止上级事件 或者e.preventDefault();
        dispatch(actionCreators.click_Icon_Item(item));
        switch (item) {
            case 1:
                alert('图层事件');
                break;
            case 2:
                //alert('漫游事件');
                Common.FlightRoaming();
                break;
            case 5:
                //alert('飞行事件');
                Common.FlightRoaming();
                break;
            case 6:
                //alert('虚拟化事件');
                More.this.Surveillance();
                break;
            case 7:
                alert('地图信息事件');
                break;
            default:
                return null;
        }
    },
    clickIconItemNav(even,item) {
        even.stopPropagation(); //阻止合成事件的冒泡
        dispatch(actionCreators.click_Icon_Item_Nav(item));
        switch (item) {
            case 1:
                //alert('测高事件');

                More.this.MeasurementHeight();
                break;
            case 2:
                //alert('测距事件');
                More.this.measureLineSpace();
                break;
            case 3:
                //alert('测面积事件');
                More.this.measureAreaSpace();
                break;
            case 4:
                //雨事件
                //alert('雨事件');
                More.this.RainwaterSystem();
                break;
            case 5:
                //雪事件
                //alert('雪事件');
                More.this.snowSystem();
                break;
            case 6:
                //alert('雾事件');
                More.this.Fogging();
                // Weather.C();
                break;
            case 7:
                //光照事件
                //alert('光照事件');
                More.this.enableLighting();
                break;
            default:
                return null;
        }
    }
});
export default connect(mapState,mapDispatch)(More);
