import React, {Component, Fragment} from 'react';
import {Common} from "./method";
import Public from "../public/public";
import axios from "axios";

class CesiumConent extends Component{
    constructor(props){
        super(props);
        this.state = {
            modelId: ''
        };
    };
    componentDidMount(){
        this.Build();//加载建筑楼层数据
    };
    //加载建筑楼层数据
    Build(){
        var Floors = [];
        axios.get(global.Url+'map/build/list',{}).then((res) => {
            const result = res.data.data;
            if(result && res.data.msg === 'success') {
                for (let i = 0; i < result.length; i++) {
                    axios.post(global.Url+'map/floor/list',{build_id:result[i].id}).then((res) => {
                        const results = res.data.data;
                        if(results && res.data.msg === 'success') {
                            Floors.push(
                                {
                                    FloorItems:result[i],
                                    Floorlists:results
                                }
                            )
                            var FloorsList=JSON.stringify(Floors);
                            //console.log("FloorsList",FloorsList,Floors);
                            sessionStorage.setItem('Floor',FloorsList);
                        }else{
                            alert(res.msg)
                        }
                    })
                }
            }else{
                alert(res.msg)
            }

        })
    }
    render(){
        return(
            <Fragment>
                <iframe
                    id="core_content"
                    name="iframeName"
                    ref="core_content"
                    title="core_content"
                    src={global.IframeUrl} //192.168.0.64
                    style={{width: '100%', height:'100%'}}
                    sandbox="allow-same-origin allow-scripts allow-popups allow-forms"
                    scrolling="auto"
                    onLoad={Common.List}
                />
                <Public/>
            </Fragment>
        )
    }

    componentWillUnmount(){
        //window.removeEventListener("message",this.receiveMessage)
    }
    receiveMessage = (event) => {
        //console.log(event);
        if(event!==undefined && event.data && event.data.name){
           // console.log('接受iframe的数据',event.data);
        }
    };
    handleClick = () =>{
        const childFrameObj = document.getElementById('core_content');
        childFrameObj.contentWindow.postMessage(1233, '*');
    };
}

export default CesiumConent;
