import React,{Fragment,PureComponent} from "react";
import {connect} from 'react-redux';
import {actionCreators} from "../../store";
import {Common} from "../../../Cesium/method";
import axios from "axios";
import $ from 'jquery';

class ConfigData1 extends PureComponent{
    constructor(props){
        super(props);
        ConfigData1.this = this;
        this.state = {
            navItemShow:false,
            Nav:true,//下拉导航显示
            navItem:0, //下拉菜单id,id为1是类别管理，id为,2是设备上图
            X:'',//输入框的x
            Y:'',//输入框的y
            Z:'',//输入框的z
            labelname:'',//标签名称
            labelid:'',//标签id
            Floor : JSON.parse(sessionStorage.getItem('Floor')),//楼层缓存信息
            CameraView : '',//视角
            CameraViewState : false,
            ShowPublic:false,
            OneHtml:true,
            TwoHtml:false,
        }
        this.CameraView = this.CameraView.bind(this);//点击当前视角
        this.AssignedLabelID = this.AssignedLabelID.bind(this);//一级菜单点击
        this.NavClick = this.NavClick.bind(this);//二级菜单点击
        this.SaveNav = this.SaveNav.bind(this);//保存二级菜单修改
    }
    componentDidMount(){
        console.log(ConfigData1.this.Floor);
        window.receiveMessageFromIndex = function ( e ) {
            if(e !== undefined){
                console.log( '我是react,我接受到了来自iframe的模型ID：', e.data);
                switch(e.data.switchName){
                    case 'GeoaddlabelV2':
                        sessionStorage.setItem('labelData',this.JSON.stringify(e.data));
                        var datas =  JSON.parse(sessionStorage.getItem('labelData')).style.position;
                        ConfigData1.this.setState({
                            X:datas.x,
                            Y:datas.y,
                            Z:datas.z
                        })
                        break;
                    case 'getCameraView':
                        ConfigData1.this.setState({
                            CameraView : this.JSON.stringify(e.data.cv)
                        })
                        console.log('数据已刷新',ConfigData1.this.state.CameraView);
                        break;
                    default:
                        return null;
                }

            }
        }
        //监听message事件
        window.addEventListener("message", window.receiveMessageFromIndex, false);

        this.initData();//初始化数据
    };
    //初始化 输入框值
    initData(){
        var Floors=ConfigData1.this.state.Floor;
        if(Floors[0].FloorItems.center_position == null){
            this.setState({
                labelname:Floors[0].FloorItems.build_name,//标签名称
                labelid:Floors[0].FloorItems.id,//标签id
            })
        }else{
            var list=JSON.stringify({x:Floors[0].FloorItems.center_position.x,y:Floors[0].FloorItems.center_position.y,z:Floors[0].FloorItems.center_position.z,h:Floors[0].FloorItems.center_position.heading,p:Floors[0].FloorItems.center_position.pitch,r:Floors[0].FloorItems.center_position.roll});
            console.log(list);
            this.setState({
                X:Floors[0].FloorItems.label_position.x,//输入框的x
                Y:Floors[0].FloorItems.label_position.y,//输入框的y
                Z:Floors[0].FloorItems.label_position.z,//输入框的z
                labelname:Floors[0].FloorItems.build_name,//标签名称
                labelid:Floors[0].FloorItems.id,//标签id
                CameraView : JSON.stringify({x:Floors[0].FloorItems.center_position.x,y:Floors[0].FloorItems.center_position.y,z:Floors[0].FloorItems.center_position.z,h:Floors[0].FloorItems.center_position.heading,p:Floors[0].FloorItems.center_position.pitch,r:Floors[0].FloorItems.center_position.roll}),//视角
            })
        }

    }
    //定位标签
    GeoaddlabelV2(){
        console.log(this.state.labelid)
        Common.GeoaddlabelV2(this.state.labelname,this.state.labelid);//labelname
    }
    //更新点击的楼id及名称
    AssignedLabelID(e,item){
        console.log(item);
        var Position=item.FloorItems.center_position;
        if(Position === null){
            console.log(item.FloorItems.id)
            this.setState({
                labelid:item.FloorItems.id,
                labelname:item.FloorItems.build_name,
                X:'',//输入框的x
                Y:'',//输入框的y
                Z:'',//输入框的z
                CameraView : '',//视角
                OneHtml:true,
                TwoHtml:false,
            })
        }else{
            this.setState({
                labelid:item.FloorItems.id,
                labelname:item.FloorItems.build_name,
                X:item.FloorItems.label_position.x,//输入框的x
                Y:item.FloorItems.label_position.y,//输入框的y
                Z:item.FloorItems.label_position.z,//输入框的z
                CameraView : JSON.stringify({x:Position.x,y:Position.y,z:Position.z,h:Position.heading,p:Position.pitch,r:Position.roll}),//视角
                OneHtml:true,
                TwoHtml:false,
            })
        }
        e.stopPropagation();
        // 被点击的<h1>
        let node = $(e.target);

        // 属于<h1>的相邻子菜单列表
        let subMenu = node.children();

        // 显示/隐藏这个列表
        subMenu.css("display", subMenu.css('display') === "none" ? "block" : "none");

    }
    /**
     * 刷新标签
     */
    RefreshLabel(){
        var that = this;
        setTimeout(function(){
            Common.ModifyLabel({id:that.state.labelid,labeltxt : that.state.labelname,x:Number(that.state.X),y:Number(that.state.Y),z:Number(that.state.Z)});
        },0)
    }
    //保存刷新数据
    Push(){
        var Floors = [];
        axios.get(global.Url+'map/build/list',{}).then((res) => {
            const result = res.data.data;
            if(result) {
                for (let index = 0; index < result.length; index++) {
                    axios.post(global.Url+'map/floor/list',{build_id:result[index].id}).then((res) => {
                        const results = res.data.data;
                        if(results) {
                            Floors.push(
                                {
                                    FloorItems:result[index],
                                    Floorlists:results
                                }
                            )
                            if(index === result.length - 1)
                            {
                                var FloorsList=JSON.stringify(Floors);
                                console.log(FloorsList);
                                sessionStorage.setItem('Floor',FloorsList);
                                ConfigData1.this.setState({
                                    Floor:Floors
                                })
                            }
                        }else{
                            alert(res.data.msg)
                        }
                    })
                }
            }else{
                alert(res.data.msg)
            }

        })
    }
    //保存当前栋数据
    SubmitBuildingInformation(){
        console.log(this.state.CameraView)
        if(!this.state.CameraView){
            alert("请先设置坐标位置！")
        }else if(this.state.X === '' || this.state.labelid === ''){
            alert("请先设置标注位置！")
        }else{
            var  CameraView =  JSON.parse(this.state.CameraView);
            var data =
                {
                    id: this.state.labelid,
                    build_name: this.state.labelname,
                    center_position: {
                        y: CameraView.y,
                        x: CameraView.x,
                        z: CameraView.z,
                        heading: CameraView.h,
                        pitch: CameraView.p,
                        roll: CameraView.r
                    },
                    label_position: {
                        x:Number(this.state.X),y:Number(this.state.Y),z:Number(this.state.Z),
                        heading: 0,
                        itch: 0,
                        roll: 0
                    },
                    remark: 1
                }
            axios.post(global.Url+'map/build/update',data).then((res) => {
                const result = res.data.data;
                if(result) {
                    alert("保存成功");
                    this.Push();
                }else{
                    alert(res.data.msg)
                }
            })
            console.log(data)
        }


    }
    //修改X坐标
    changeX(event){
        this.setState({
            X: event.target.value
        });
        this.RefreshLabel();
        console.log(event.target.value)
    }
    //修改Y坐标
    changeY(event){
        this.setState({
            Y: event.target.value
        });
        this.RefreshLabel();
        console.log(event.target.value)
    }
    //修改Z坐标
    changeZ(event){
        this.setState({
            Z: event.target.value
        });
        this.RefreshLabel();
        console.log(event.target.value)
    }
    //修改名称
    Modifylabelname(event){
        this.setState({
            labelname: event.target.value
        });
        this.RefreshLabel();
        console.log(event.target.value)
    }
    //修改坐标定位
    CameraView(){
        var Dates= JSON.parse(ConfigData1.this.state.CameraView);
        if(Dates===null){
            alert("请先进行定位！");
        }else{
            console.log(Dates);
            var cv = {
                x:Dates.x,
                y:Dates.y,
                z:Dates.z,
                h: Dates.h,
                p: Dates.p,
                r: Dates.r,
            };
            var frame=document.getElementById("core_content");
            var sendData={
                Event : '',
                ModName : "RetrospectiveCameraView",
                parameter :cv
            };
            frame.contentWindow.postMessage(sendData,"*");
        }

    }
    //点击二级菜单
    NavClick(e,name){
        e.stopPropagation();
        var centerPosition=name.center_position;
        console.log(name);
        if(centerPosition===null){
            this.setState({
                labelname:name.floor_name,
                CameraView : JSON.stringify(centerPosition),//视角
                OneHtml:false,
                TwoHtml:true
            })
        }else{
            this.setState({
                labelname:name.floor_name,
                CameraView : JSON.stringify({x:centerPosition.x,y:centerPosition.y,z:centerPosition.z,h:centerPosition.heading,p:centerPosition.pitch,r:centerPosition.roll}),//视角
                OneHtml:false,
                TwoHtml:true
            })
        }
        sessionStorage.setItem('navId',name.id);
    }
    //保存二级菜单修改
    SaveNav(){
        var Id=sessionStorage.getItem('navId');
        var center=JSON.parse(ConfigData1.this.state.CameraView);
        if(center===null){
            alert("请先进行定位!");
        }else{
            var data={
                "id": Id,
                "floor_name": ConfigData1.this.state.labelname,
                "center_position": {
                    "y": center.y,
                    "x": center.x,
                    "z": center.z,
                    "heading": center.h,
                    "pitch": center.p,
                    "roll": center.r
                },
                "is_underground": true,
                "rise_height": 10,
                "remark": "1F"
            }
            axios.post(global.Url+'map/floor/update',data).then((res) => {
                const results = res.data.data;
                if(results) {
                    alert("保存成功");
                    this.Push();
                }else{
                    alert(res.data.msg)
                }
            })
        }

    }
    render(){
        const {Floor,X,Y,Z,labelname,CameraView,OneHtml,TwoHtml} = this.state;
        const {navClick} = this.props;
        return(
            <Fragment>
                <div className="configShow" style={{'width':'653px','height': '590px'}}>
                    <div className="configShowNav">建筑配置 <i onClick={() => navClick(100)}></i></div>
                    <div className="NavigationButtonBorder">
                        <div className="configBut" style={{'marginTop': '0px'}}><button style={{'position':'absolute','marginLeft':'20px'}}>标注配置</button><button style={{'marginRight': '380px'}}>操作配置</button></div>
                    </div>
                    <div className="DataConfigTreeDiv">
                        <ul>
                            {
                                Floor.length > 0 && Floor.map(
                                    (item,index) => {
                                        return  <li key={index} onClick={(e)=>this.AssignedLabelID(e,item)}>{item.FloorItems.build_name}
                                            <ul className='navItemList'>
                                                {
                                                    item.Floorlists.length > 0 && item.Floorlists.map(
                                                        (name,i) => {
                                                            return  <li key={i} onClick={(e)=>this.NavClick(e,name)}>{name.floor_name}</li>
                                                        }
                                                    )
                                                }
                                            </ul>
                                        </li>
                                    }
                                )
                            }
                        </ul>
                    </div>
                    {OneHtml
                        ?
                        <div className="DataOne">
                            <h1>建筑</h1>
                            <li><span>建筑名称：</span><input type="text" value={labelname} onChange={(event) => this.Modifylabelname(event)}/></li>
                            <li><span>建筑标注：</span><button onClick={()=>this.GeoaddlabelV2()}>设置标注</button></li>
                            <li>
                                <span>标注位置：</span>
                                <div className="DataRight">
                                    <span><label>X：</label><input type="text" defaultValue={X} onChange={(event) => this.changeX(event)}/></span>
                                    <span><label>Y：</label><input type="text" defaultValue={Y} onChange={(event) => this.changeY(event)}/></span>
                                    <span><label>Z：</label><input type="text" defaultValue={Z} onChange={(event) => this.changeZ(event)}/></span>
                                </div>
                            </li>
                            <li><span>定位坐标：</span>
                                <span className='newNav'>
                                <li  onClick={() => this.CameraView()}>坐标定位</li><li className='next' onClick={() => Common.onCoordinate()}>当前位置</li>
                                <div className="liCont">{CameraView}</div>
                            </span>
                            </li>
                            <div className="configBut" onClick={() => this.SubmitBuildingInformation()}><button>保存</button></div>
                        </div>
                        :''
                    }
                    {TwoHtml
                        ?
                        <div className="DataOne">
                            <h1>楼层</h1>
                            <li><span>楼层名称：</span><input type="text" defaultValue='' value={labelname} onChange={(event) => this.Modifylabelname(event)}/></li>
                            <li><span>定位坐标：</span>
                                <span className='newNav'>
                                <li  onClick={() => this.CameraView()}>坐标定位</li><li className='next' onClick={() => Common.onCoordinate()}>当前位置</li>
                                <div className="liCont">{CameraView}</div>
                            </span>
                            </li>
                            <div className="configBut" onClick={() => this.SaveNav()}><button>保存</button></div>
                        </div>
                        :''
                    }
                </div>
            </Fragment>
        )
    }
}

const mapDispatch = (dispatch) => ({
    navClick(id,item) {
        dispatch(actionCreators.nav_click(id,item));
    },
});

export default connect(null,mapDispatch)(ConfigData1);