import React,{PureComponent} from 'react';
import '../../../style/common.css';
import {connect} from 'react-redux';
import {actionCreators} from "../store";
import  {Common} from "../../Cesium/method";

class Home extends PureComponent{
    constructor(){
        super()
        this.state ={
            activeBtn:false
        }
        this.changeStatus = this.changeStatus.bind(this)
    }
    changeStatus(){
        this.setState({
            activeBtn:!this.state.activeBtn
        })
    }
    render(){
        const {clickIcon,index} = this.props;
        const {activeBtn} = this.state;
        return(
            <li type='button' onClick={() => {clickIcon(1);this.changeStatus()}}  className={activeBtn && index === 1 ? 'active' :''} >
                <i className="icon1"></i>复位
            </li>

        )
    }
}
const mapState = (state) => {
    return{
        index:state.footer.get('index')
    }
};
const mapDispatch = (dispatch) => ({
    clickIcon(item) {
        dispatch(actionCreators.click_Icon(item));
        Common.onResetCoordinates()
    },

});
export default connect(mapState,mapDispatch)(Home);


